@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;700;900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

html {
  background: #0a0a0f;
}

/* FORCE LANDSCAPE */
@media (orientation: portrait) {
  html {
    transform: rotate(-90deg);
    transform-origin: left top;
    width: 100vh;
    height: 100vw;
    overflow: hidden;
    position: absolute;
    top: 100%;
    left: 0;
  }
}

body {
  font-family: 'Orbitron', sans-serif;
  background: #0a0a0f;
  color: white;
  overflow: hidden;
}

.controller {
  display: flex;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(135deg, #0a0a0f 0%, #1a1a2f 100%);
}

/* LEFT SIDE */
.left-side {
  flex: 1;
  padding: 15px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  border-right: 1px solid #333;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.title {
  font-size: 1.4rem;
  font-weight: 900;
  background: linear-gradient(90deg, #0f0, #0ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.dot {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background: #f33;
  box-shadow: 0 0 10px #f33;
}

.dot.on {
  background: #0f0;
  box-shadow: 0 0 10px #0f0;
}

/* BUTTONS */
.buttons {
  display: flex;
  gap: 10px;
}

.btn {
  flex: 1;
  padding: 12px;
  font-family: 'Orbitron', sans-serif;
  font-size: 0.7rem;
  font-weight: 700;
  border: 2px solid;
  background: transparent;
  border-radius: 8px;
  cursor: pointer;
}

.btn.blue { border-color: #0cf; color: #0cf; }
.btn.red { border-color: #f33; color: #f33; }
.btn.green { border-color: #0f0; color: #0f0; }
.btn.green.active { background: #0f0; color: #000; }
.btn:disabled { opacity: 0.3; }
.btn:active { transform: scale(0.95); }

/* STEERING */
.steering {
  background: #151520;
  border-radius: 10px;
  padding: 15px;
  border: 1px solid #333;
}

.steering-bar {
  height: 20px;
  background: #0a0a0f;
  border-radius: 10px;
  position: relative;
  margin-bottom: 8px;
}

.steering-indicator {
  position: absolute;
  top: 50%;
  width: 24px;
  height: 24px;
  background: linear-gradient(135deg, #0ff, #0f0);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 15px #0ff;
  transition: left 0.05s;
}

.steering-labels {
  display: flex;
  justify-content: space-between;
  font-size: 0.65rem;
  color: #666;
}

.steering-labels span:nth-child(2) {
  color: #0ff;
  font-size: 1.2rem;
  font-weight: 700;
}

/* TERMINAL */
.terminal {
  flex: 1;
  background: #050508;
  border: 1px solid #222;
  border-radius: 6px;
  padding: 8px;
  font-family: monospace;
  font-size: 0.6rem;
  overflow: hidden;
}

.terminal div {
  color: #555;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.terminal div.out { color: #f80; }
.terminal div.in { color: #0cf; }

.hint {
  font-size: 0.6rem;
  color: #444;
  text-align: center;
}

/* RIGHT SIDE - SPEED */
.right-side {
  width: 100px;
  background: #101015;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px 10px;
}

.speed-title {
  font-size: 0.7rem;
  font-weight: 700;
  color: #f80;
  margin-bottom: 8px;
}

.slider-area {
  flex: 1;
  width: 50px;
  position: relative;
}

.slider {
  writing-mode: vertical-lr;
  direction: rtl;
  width: 100%;
  height: 100%;
  -webkit-appearance: none;
  background: transparent;
  position: absolute;
  z-index: 10;
  cursor: pointer;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 44px;
  height: 26px;
  background: linear-gradient(135deg, #f80, #f50);
  border-radius: 6px;
  box-shadow: 0 0 15px rgba(255,136,0,0.5);
}

.slider::-moz-range-thumb {
  width: 44px;
  height: 26px;
  background: linear-gradient(135deg, #f80, #f50);
  border-radius: 6px;
  border: none;
}

.slider-bg {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 24px;
  height: 100%;
  background: #050508;
  border-radius: 12px;
  border: 2px solid #333;
  overflow: hidden;
}

.slider-fill {
  position: absolute;
  bottom: 0;
  width: 100%;
  background: linear-gradient(to top, #f80, #f33);
  transition: height 0.1s;
}

.speed-num {
  font-size: 1.3rem;
  font-weight: 900;
  color: #f80;
  margin: 8px 0;
}

.stop-btn {
  width: 100%;
  padding: 10px;
  font-family: 'Orbitron', sans-serif;
  font-size: 0.7rem;
  font-weight: 700;
  background: #f33;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.stop-btn:active {
  transform: scale(0.95);
}
